.h5_component_bar{
	
}
.h5_component_bar .line{
	height: 15px;
	font-size: 12px;
	line-height: 15px;
	margin-bottom: 15px;
}
.h5_component_bar .name{
	width: 60px;
	float: left;
	color: #000;
	text-align: center;
}
.h5_component_bar .rate{
	height: 15px;
	margin-left: 5px;
	float: left;
	position: relative;
}
.h5_component_bar .rate .bg{
	background-color: #99c0ff;
	height: 15px;
	width: 0%;
	position: absolute;
	left: 0;
	top:0;
}

.h5_component_bar .per{
	width: 20px;
	color: #99c0ff;
	margin-left: 5px;
	float: left;
	-webkit-opacity: 0;
}

.h5_component_bar_load  .rate .bg{
	-webkit-transition: all 1s 0.5s;
	width: 100%;
}

.h5_component_bar_load  .line .per{
	-webkit-transition: all 1s 1.5s;
	-webkit-opacity: 1;
}
.h5_component_bar_leave  .line .per{
	-webkit-opacity: 0;
}
.h5_component_bar_leave .rate .bg{
	width: 0%;
}
